<template>
  <div class="container">
    <div class="blog">
      <article class="entry entry-single" style="width:100%">
        <h2 class="entry-title" style="text-align: center;">{{ quest.q_title }}</h2>
        <div class="entry-meta">
          <ul>
            <li class="d-flex align-items-center"><i class="icofont-user"></i>{{ quest.id }}</li>
            <li class="d-flex align-items-center">
              <i class="icofont-wall-clock"></i
              ><time datetime="2020-01-01">{{ quest.q_time }}</time>
            </li>
          </ul>
        </div>
        <div class="entry-content" style="width:100%; height:500px; overflow: auto;">
          <div
            class="form-control"
            name="content"
            style="padding:30px; height:100%; background-color:#ffffff; resize:none;"
            v-html="enterToBr(quest.q_content)"
          ></div>
        </div>
        <!-- form-group -->
        <div class="pricing" style="text-align: center; margin-top:30px; ">
          <router-link to="/" class="btn-buy">목록</router-link>
          <router-link :to="`/modify/${quest.q_no}`" class="btn-buy">수정</router-link>
          <a href="#" class="btn-buy" @click="deleteQuest">삭제</a>
        </div>
      </article>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import http from "@/util/http-common";

export default {
  computed: {
    ...mapGetters(["quest"]),
  },
  methods: {
    deleteQuest() {
      if (confirm("삭제할까요?")) {
        http.delete(`/question/${this.$route.params.q_no}`).then(({ data }) => {
          let msg = "삭제 처리시 문제가 발생했습니다.";
          if (data === "success") {
            msg = "삭제가 완료되었습니다.";
          }
          alert(msg);
          this.$router.push("/");
        });
      }
    },
    enterToBr(str) {
      return String(str).replace(/(?:\r\n|\r|\n)/g, "<br />");
    },
  },
};
</script>
