<template>
  <div>
    <view-detail></view-detail>
    <div class="container">
      <div clss="blog">
        <article class="entry entry-single" style="width:100%">
          <comment-write
            v-if="isModifyShow && this.modifyComment != null"
            :modifyComment="this.modifyComment"
            @modify-comment-cancel="onModifyCommentCancel"
          />
          <comment-view
            v-else
            v-for="(comment, index) in comments"
            :key="index"
            :comment="comment"
            @modify-comment="onModifyComment"
          />
        </article>
      </div>
    </div>
    <comment-write :q_no="q_no" />
  </div>
</template>

<script>
import ViewDetail from "@/components/include/ViewDetail.vue";

import { mapGetters } from "vuex";
import CommentWrite from "@/components/include/comments/CommentWrite.vue";
import CommentView from "@/components/include/comments/Comment.vue";

export default {
  data() {
    return {
      q_no: Number,
      isModifyShow: false,
      modifyComment: Object,
    };
  },
  computed: {
    ...mapGetters(["comments"]),
  },
  components: {
    ViewDetail,
    CommentWrite,
    CommentView,
  },
  created() {
    this.q_no = this.$route.params.q_no;
    this.$store.dispatch("getQuest", this.q_no);
    this.$store.dispatch("getQuests");
    this.$store.dispatch("getComments", this.q_no);
  },
  methods: {
    onModifyComment(comment) {
      this.modifyComment = comment;
      this.isModifyShow = true;
    },
    onModifyCommentCancel(isShow) {
      this.isModifyShow = isShow;
    },
  },
};
</script>
