<template>
  <div class="container">
    <div class="blog">
      <article class="entry entry-single" style="width:100%">
        <div class="entry entry-single" style="width:100%">
          <div v-if="this.modifyComment != null" class="container row">
            <textarea
              name="comment"
              v-model="modifyComment.comment"
              class="form-control"
              rows="2"
              col="35"
            ></textarea>
            <button @click="updateCommentCancel" class="my-btn">취소</button>
            <button @click="updateComment" class="my-btn">수정</button>
          </div>
          <div v-else class="conatiner row">
            <textarea
              name="comment"
              v-model="comment"
              class="form-control"
              cols="35"
              rows="2"
              placeholder="무분별한 비방이나 욕설은 확인 즉시 삭제됩니다"
            ></textarea>
            <button @click="registComment" class="my-btn">등록</button>
          </div>
        </div>
      </article>
    </div>
  </div>
</template>

<script>
import http from "@/util/http-common";
export default {
  name: "commentwrite",
  data() {
    return {
      // 로그인한 사용자이름을 받아와야 한다.
      user_id: "ssafy",
      comment: "",
      comment_no: "",
    };
  },
  props: {
    q_no: String,
    modifyComment: Object,
  },
  methods: {
    registComment() {
      console.log(this.user_id);
      http
        .post("/comments/", {
          comment_no: this.comment_no,
          user_id: this.user_id,
          comment: this.comment,
          q_no: this.q_no,
        })
        .then(({ data }) => {
          let msg = "등록중 문제가 발생하였습니다.";
          if (data === "success") {
            msg = "등록이 완료되었습니다.";
          }
          alert(msg);
          //글삭제
          this.comment = "";
          //댓글 얻기
          this.$store.dispatch("getComments", this.q_no);
        });
    },
    updateComment() {
      http
        .put(`/comments`, {
          comment_no: this.modifyComment.comment_no,
          comment: this.modifyComment.comment,
        })
        .then(({ data }) => {
          let msg = "수정 처리시 문제가 발생했습니다.";
          if (data === "success") {
            msg = "수정이 완료되었습니다.";
          }
          alert(msg);

          // 도서평(댓글) 얻기.
          this.$store.dispatch("getComments", this.modifyComment.q_no);
          this.$emit("modify-comment-cancel", false);
        });
    },
    updateCommentCancel() {
      this.$emit("modify-comment-cancel", false);
    },
  },
};
</script>
<style scoped>
.my-btn {
  float: right;
  margin-left: 10px;
}
textarea {
  width: 80%;
}
</style>
