<template>
  <div class="blog">
    <article class="entry entry-single" style="width:100%">
      <div class="entry entry-single" style="width:100%">
        <div v-show="isShow" class="comment clearfix">
          <h5>
            {{ comment.user_name }}
            &nbsp;&nbsp;
            <time datetime="2020-01-01">{{ comment.comment_time }}</time>
            <a href="#" class="reply" @click="modifyCommentView"><i class="icofont-edit"></i></a>
            <a href="#" class="reply" @click="deleteComment"><i class="icofont-trash"></i></a>
          </h5>
          <div class="content" v-html="enterToBr(comment.comment)"></div>
        </div>
      </div>
    </article>
  </div>
</template>

<script>
import http from "@/util/http-common";
export default {
  name: "comment",
  data() {
    return {
      isShow: true,
    };
  },
  props: {
    comment: {},
  },
  methods: {
    modifyCommentView() {
      this.$emit("modify-comment", {
        comment_no: this.comment.comment_no,
        comment: this.comment.comment,
        q_no: this.comment.q_no,
      });
    },
    deleteComment() {
      if (confirm("삭제하시겠습니까?")) {
        http.delete(`/comments/${this.comment.comment_no}`).then(({ data }) => {
          let msg = "삭제중 오류가 발생하였습니다.";
          if (data === "success") {
            msg = "삭제를 완료했습니다.";
          }
          alert(msg);

          this.$store.dispatch("getComments", this.comment.q_no);
        });
      }
    },
    enterToBr(str) {
      return String(str).replace(/(?:\r\n|\r|\n)/g, "<br />");
    },
  },
};
</script>

<style scoped>
.blog-comments .comment {
  margin-top: 30px;
  position: relative;
}
.blog-comments .comment h5 a {
  font-weight: bold;
  color: #313030;
  transition: 0.3s;
}
i {
  float: right;
}
time {
  color: #afa29e;
}
</style>
